}
.main
.tab {
  width: 100%;
  height: 2rem;
  line-height: 2rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-bottom: 1px solid rgba(7, 17, 27, .1);
}
.tab a {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  font-size: .7rem;
  color: rgb(77, 85, 93);
  display: block;
}
.tab a.active {
  color: rgb(250, 20, 20);
}




