@font-face {
  font-family: 'eleme-icon';
  src:  url('../fonts/eleme-icon.eot?ud6iwk');
  src:  url('../fonts/eleme-icon.eot?ud6iwk#iefix') format('embedded-opentype'),
    url('../fonts/eleme-icon.ttf?ud6iwk') format('truetype'),
    url('../fonts/eleme-icon.woff?ud6iwk') format('woff'),
    url('../fonts/eleme-icon.svg?ud6iwk#eleme-icon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'eleme-icon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

